<template lang="">
<div class="_full_router financial-detail-content">
  <div class="app-header">
    <div class="top-back" @click="goBack">
      <span class="iconfont icon-return-arrow"></span>
    </div>
    <div class="center">
      详情
    </div>
  </div>
  <div class="financial-detail-message">
    <h1 style="margin:6px 0;">{{financial.stkName}}</h1>
    <div class="financial-detail-box market-detail-box">
      <div style="">
        <div style="font-size:24px" class="red">{{financial.hqzjcj | toMoney }}</div>
      </div>
      <div>
        <div>高<span class="red">{{financial.hqzgcj | toMoney}}</span></div>
        <div>低<span class="red">{{financial.hqzdcj | toMoney}}</span></div>
      </div>
      <div>
        <div>开<span class="red">{{financial.hqjrkp  | toMoney}}</span></div>
        <div>开<span class="red">{{financial.hqjrkp  | toMoney}}</span></div>
      </div>
      <div>
        <div>量<span class="">{{financial.hqcjsl }}</span></div>
        <div>额<span class="">{{financial.hqcjje | toMoney}}</span></div>
      </div>
    </div>
  </div>
  <div class="financial-detail-list">
    <label for="" style="font-size:12px;color:#999;">买入队列</label>
    <div v-if="quoteQueueBuy.length==0" style="height:100px;line-height:100px;text-align:center;font-size:24px;color:#777;">
      暂无买入队列
    </div>
    <ul class="list-infinite ">
      <li v-for="(item,index) in quoteQueueBuy">

        <div class="financial-box">
          <div>
            <div class="">{{ item.xyydh}}</div>
          </div>
          <div>
            <div class="red">{{ item.xywtjg | toMoney}}</div>
            <!-- 价格格式化-->
          </div>
          <div>
            <div>{{item.xywtsl}}</div>
          </div>
          <div @click="bargainToSell(index)">卖出</div>
        </div>
      </li>
    </ul>
    <label for="" style="font-size:12px;color:#999;">卖出队列</label>
    <div v-if="quoteQueueSell.length==0" style="height:100px;line-height:100px;text-align:center;font-size:24px;color:#777;">
      暂无卖出队列
    </div>
    <ul class="list-infinite ">
      <li v-for="(item,index) in quoteQueueSell">

        <div class="financial-box">
          <div>
            <div class="">{{ item.xyydh}}</div>
          </div>
          <div>
            <div class="red">{{ item.xywtjg | toMoney}}</div>
            <!-- 价格格式化-->
          </div>
          <div>
            <div>{{item.xywtsl}}</div>
          </div>
        </div>
      </li>
    </ul>
  </div>
  <div class="app-footer normal-box">
    <div class="center" @click="goPayBuy">
      定价买入
    </div>
    <div class="center" @click="goPaySell" style="background:#999;">
      定价卖出
    </div>
  </div>
</div>
</template>
<script>
import cookieUtil from "js-cookie";
import {
  Navbar,
  TabItem,
  InfiniteScroll
} from "mint-ui";
import {
  Toast
} from "mint-ui";
import {
  Progress
} from 'mint-ui';
import {
  Cell
} from 'mint-ui';

Vue.component(Cell.name, Cell);
Vue.component(Progress.name, Progress);
Vue.use(InfiniteScroll);

export default {
  data() {
    return {
      cacheList: {},
      quoteQueueBuy: [],
      quoteQueueSell: [],
      financial: {
        hqzjcj: "", //现价
        hqjrkp: "", //今开
        hqzgcj: "", //最高
        hqzdcj: "", //最低
        hqzjcl: "", //现量
        hqcjjj: "", //均价
        hqcjsl: '', //总量
        hqcjje: "", //总额
      },
      prdType: "",
      salesTarget: ""
    };
  },
  computed: {

    jindu: function() {
      return (this.financial.issAmt - this.ktje) / this.financial.issAmt * 100 == 0 ? 1 : (this.financial.issAmt - this.ktje) / this.financial.issAmt * 100;
    },
    sssj: function() {
      return dateFormatter(this.financial.listDate) + " " + timeFormatter(this.financial.listTime);
    },
    zzsj: function() {
      return dateFormatter(this.financial.endDate) + " " + timeFormatter(this.financial.endTime);
    },
    qsrq: function() {
      return dateFormatter(this.financial.issBgnDate) + " " + timeFormatter(this.financial.issBgnTime);
    },
    jzrq: function() {
      return dateFormatter(this.financial.issEndDate) + " " + timeFormatter(this.financial.issEndTime);
    }
  },
  created: function() {
    let self = this;
    var zqdm = this.$route.query.hqzqdm;
    var par = {
      hqzqdm: zqdm,
      hqscdm:global.scdm
    };
    axiosUtil.quoteSummary(par).then(function(data) {
      var data = data.data;
      self.financial = data.rows[0];
    })

    var par2 = {
      xyscdm: global.scdm,
      xyywlb: global.STKBIZTYPE.PRICE_SELL_APPL,
      xyzqdm: zqdm,
    }
    axiosUtil.quoteQueue(par2).then(function(data) {
      var data = data.data;
      self.quoteQueueSell = data.rows;
    })
    var par3 = {
      xyscdm: global.scdm,
      xyywlb: global.STKBIZTYPE.PRICE_BUY_APPL,
      xyzqdm: zqdm,
    }
    axiosUtil.quoteQueue(par3).then(function(data) {
      var data = data.data;
      self.quoteQueueBuy = data.rows;
    })
  },
  filters: {},
  methods: {
    goBack: function() {
      this.$router.go(-1);
    },
    goPayBuy: function() {
      //hqzqdm
      this.$router.push("/offer?hqzqdm=" + this.$route.query.hqzqdm + "&type=5B&orderPrice=" + this.financial.hqzjcj);
    },
    goPaySell: function() {
      //hqzqdm
      this.$router.push("/offer?hqzqdm=" + this.$route.query.hqzqdm + "&type=5S&orderPrice=" + this.financial.hqzjcj);
    },
    bargainToSell: function(index) {
      //hqzqdm

      this.$router.push("/offer?hqzqdm=" + this.$route.query.hqzqdm + "&type=6S&bargainOrderPrice=" + this.quoteQueueBuy[index].xywtjg + "&conferNo=" + this.quoteQueueBuy[index].xyydh);
    },
     bargainToBuy: function(index) {
      //hqzqdm

      this.$router.push("/offer?hqzqdm=" + this.$route.query.hqzqdm + "&type=6S&bargainOrderPrice=" + this.quoteQueueBuy[index].xywtjg + "&conferNo=" + this.quoteQueueBuy[index].xyydh);
    }
  }
};
</script>
<style scoped>
.financial-box {
  display: -webkit-flex;
  /* Safari */
  display: flex;
}

.financial-box>div {
  flex-grow: 1;
  width: 33.33%;
}

.financial-detail-box {
  display: -webkit-flex;
  /* Safari */
  display: flex;
  font-size: 12px;
  color: #abb6ba;
}

.financial-detail-box>div {
  flex-grow: 1;
  width: 33.33%;
}

.market-detail-box>div {
  width: 25%;
}

.nhsyl {
  font-size: 26px;
  color: #ef4f4f;
}

.nhsyl-text {
  font-size: 14px;
  color: #bbb;
}

.qx {
  margin-top: 12px;
}

.qx-text {
  font-size: 14px;
  color: #7089ff;
  border-right: 1px solid #ddd;
  padding: 0 4px;
  margin-right: 4px;
}

.app-content {
  height: 100%;
  background: #111;
}

.financial-content {
  height: 100%;
  background: #efeee9;
}

::-webkit-scrollbar {
  width: 0px;
  height: 1px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.2);
}

.app-header {
  position: relative;
}

.financial-detail-content {
  background: #efeee9;
}

.financial-detail-message {
  background: #fff;
  padding: 12px;
}

.nhsy,
.fqx {
  font-size: 18px;
}

.financial-detail-list {
  margin-top: 6px;
}

.list-infinite li {
  margin-top: 6px;
  margin-bottom: 12px;
  padding: 4px 6px;
}
</style>



